import React from "react";
import { contact } from "../appCrendentials";
import { Map, GoogleApiWrapper } from "google-maps-react";
require("dotenv").config();

const mapStyles = {
  width: "100%",
  height: "100%",
};

function ContactQuick(props) {
  return (
    <div className="contact-quick">
      <div className="contact-quick-section1">
        <h2>{contact.quickContact.header}</h2>
        <p>{contact.quickContact.body}</p>
        <div>
          {contact.body.map((d, key) => (
            <div key={key}>
              <i className={d.icon}></i>
              <p>
                <p>{d.type}</p>
                <p>{d.value}</p>
              </p>
            </div>
          ))}
        </div>
      </div>
      <div className="contact-quick-section2">
        <Map
          google={props.google}
          zoom={14}
          style={mapStyles}
          initialCenter={{
            lat: -1.2884,
            lng: 36.8233,
          }}
        />
      </div>
    </div>
  );
}
export default GoogleApiWrapper({
  apiKey: "AIzaSyB_v2WLPyOwL9WlOOSvY_ZAgV29pByaXe4",
})(ContactQuick);
